<template>
  <div
      class="flex font-main overflow-hidden flex-col justify-between min-h-screen bg-gradient-to-r from-blue-400 to-indigo-200">

    <!--    SVG images for all project-->
    <SVGDefs/>

    <!--    Header-->
    <header class="mt-2 lg:mt-4">
      <div class="container flex flex-wrap justify-between items-center text-white relative focus:outline-none">

        <!--          Logo-->
        <router-link class="text-2xl font-bold" to="/">Logo</router-link>

        <!--          Hamburger button-->
        <button @click="mobileMenu = !mobileMenu"
                class="
                  h-12 w-12 flex flex-col justify-around items-center py-1 order-first focus:outline-none
                  sm:hidden"
                type="button">
          <span class="h-1 bg-white block transform transition"
                :class="mobileMenu ? ['-rotate-45', 'translate-y-1', 'w-7'] : 'w-4/5'"></span>
          <span class="h-1 w-4/5 bg-white block transform transition"
                :class="mobileMenu ? 'translate-x-48' : ''"></span>
          <span class="h-1 bg-white block transform transition"
                :class="mobileMenu ? ['rotate-45', '-translate-y-1', 'w-7'] : 'w-4/5'"></span>
        </button>

        <!--          Navigation menu-->
        <div class="
              max-w-28 flex flex-col mt-4 absolute top-full z-20 transition-spacing duration-500 ease-in-out
              sm:max-w-auto sm:static sm:flex-row sm:mt-0"
            :class="[mobileMenu ? 'ml-0' : '-ml-28']">
          <router-link class="sm:text-sm md:text-base uppercase" to="/">Home</router-link>
          <router-link class="sm:ml-4 sm:text-sm md:text-base lg:ml-12 uppercase" to="/portfolio">Portfolio</router-link>
          <router-link class="sm:ml-4 sm:text-sm md:text-base lg:ml-12 uppercase" to="/tools">Tools</router-link>
          <router-link class="sm:ml-4 sm:text-sm md:text-base lg:ml-12 uppercase" to="/contacts">Contacts</router-link>
        </div>
      </div>
    </header>

    <!--    Main-->
    <main class="relative">
      <div class="
        container relative z-10 transition-spacing duration-500 ease-in-out ml-0
        sm:ml-auto
        lg:"
           :class="[mobileMenu ? 'ml-28' : '']">
        <transition
            name="fade"
            mode="out-in"
            @beforeLeave="beforeLeave"
            @enter="enter"
            @afterEnter="afterEnter">
          <router-view/>
        </transition>
      </div>
    </main>

    <!--    Footer-->
    <footer class="lg:bg-white relative z-10">
      <div class="text-center pt-4 pb-2 text-sm text-white lg:text-gray-300">Teo Reat 2021</div>
    </footer>
  </div>
</template>

<script>
import SVGDefs from "@/components/SVGDefs";

export default {
  components: {
    SVGDefs
  },

  data: () => {
    return {
      mobileMenu: false,
      activeClass: 'ml-28',
      prevHeight: 0,
    }
  },

  watch: {
    $route: function () {
      this.mobileMenu = false
    }
  },
  methods: {
    beforeLeave(element) {
      this.prevHeight = getComputedStyle(element).height;
    },
    enter(element) {
      const {height} = getComputedStyle(element);

      element.style.height = this.prevHeight;

      setTimeout(() => {
        element.style.height = height;
      });
    },
    afterEnter(element) {
      element.style.height = 'auto';
    },
  },
}
</script>